import { Attachment, Post } from "../../types";
import { useState, useEffect } from 'react'
import PostService from "../../services/PostService";

type Props = {
    post:Post,
    onClick:any,
    showAllArtists:boolean,
    imagesPerRow:number,
    showPool:any
}

const Image: React.FC<Props> = ({post, onClick, showAllArtists, imagesPerRow, showPool}:Props) => {
    const [imageLoaded, setImageLoaded] = useState<boolean>(false);
    const [extended, setExtended] = useState(false);
    const [attachments, setAttachments] = useState<{name:string, path:string}[]|null>(null);

    useEffect(() => {
        const aincludes = (at:Attachment[], a:Attachment) => {
            for(let aa of at){
                if(aa.path === a.path){
                    return true;
                }
            }
            return false;
        }

        const extensions = ['png', 'jpg', 'jpeg', 'gif']
        let _attachments = post.attachments.filter(e => {
            const split = e.path.split('.')
            const extension = split[split.length-1]
            return extensions.includes(extension)
        })
        if (post.file.path){
            const split = post.file.path.split('.')
            if(extensions.includes(split[split.length-1]) && !aincludes(_attachments, post.file)){
                _attachments = [post.file, ..._attachments]
            }
        }
        if(_attachments.length !== 0){
            setAttachments(_attachments)
        }
    }, [])

    const handleImageLoad = () => {
        setImageLoaded(true);
    }

    return (
        <>
            {attachments && <div style={{width: `${100 / imagesPerRow}%`}}>
                {/*post.file.ext === 'webm' && <h3>Video</h3>*/}
                <img style={{width: '100%', display: imageLoaded ? '' : 'none'}} src={`https://kemono.party/thumbnail/data${attachments[0].path}`} alt={String(post.title)} onClick={onClick} onLoad={handleImageLoad}/>
                {!imageLoaded && <img style={{width: '100%'}} src="/kemono/loading.gif"/>}
                {showAllArtists && <div style={{display: 'flex', justifyContent: 'space-around'}}>
                    {/*<a className='highlight-link' href={post.file.url} target="_blank">original</a>*/}
                    <div>
                        <p style={{margin: 0, padding: 0}}>
                            <a href={`https://kemono.party/${post.service}/user/${post.user}/`} target='_blank' className='highlight-link' style={{color: "white", textDecoration: "none"}}>
                                {PostService.getCreatorFromId(post.user)?.name}</a> <span className={PostService.getCreatorIds().includes(post.user) ? 'artist-green' : 'artist-red'}>
                                {PostService.getCreatorIds().includes(post.user) ? '' : ''}
                            </span>
                        </p>
                    </div>
                    {attachments.length > 1 && <p style={{margin: 0, padding: 0}}>
                        <a href='#' onClick={event => showPool(event, attachments)} className='highlight-link'>pool</a>
                    </p>}
                    <a className='highlight-link' href={`https://kemono.party/${post.service}/user/${post.user}/post/${post.id}`} target="_blank">kemono</a>
                    <a href='#' onClick={event => {event.preventDefault(); setExtended(e=>!e)}}>{extended ? '' : ''}</a>
                </div>}
                {extended &&
                    <div>
                        {post.title}
                    </div>}
            </div>}
        </>
    )
}

export default Image;