import axios from "axios";
import { Post, Creator } from "../types";
import Cookies from "universal-cookie";

let cookies = new Cookies();

const devMode = false;

const getPosts = async (page:number, creators:Creator[]) => {
    const o = page * 50
    let posts:Post[] = []
    for(let creator of creators){
        //let url = `http://localhost:8001/api/kemono/get/${creator.service}/${creator.id}/${o}`
        let url = `https://api.codetabs.com/v1/proxy/?quest=https://kemono.party/api/${creator.service}/user/${creator.id}?o=${o}`
        console.log(url)
        let res = await axios.get<Post[]>(url)
        posts = [...posts, ...res.data]
    }
    return posts;
}

const addCreator = (creator:Creator) => {
    let creators:Creator[] = getCreators();
    creators.push(creator)
    cookies.set('creators', JSON.stringify(creators));
};

const addCreators = (cs:Creator[]) => {
    let creators:Creator[] = getCreators();
    creators = [...creators, ...cs]
    cookies.set('creators', JSON.stringify(creators));
};

const removeAllCreators = () => {
    cookies.set('creators', []);
}

const removeCreator = (creator:Creator) => {
    let creators = getCreators();
    const splice = creators.splice(creators.indexOf(creator), 1)
    devMode && console.log(splice);
    cookies.set('creators', creators);
    if (creators.length === 0) {
        removeAllCreators()
    }
}

const getCreators = ():Creator[] => {
    let tagstring:Creator[]|null = cookies.get('creators');
    if (tagstring == null){
        return [];
    } else {
        return tagstring.sort();
    }
}

const getCreatorIds = ():string[] => {
    const creators = getCreators();
    let result = []
    for(let creator of creators){
        result.push(creator.id)
    }
    return result
}

const getCreatorFromId = (id:string):Creator|null => {
    const creators = getCreators()
    for(let c of creators){
        if(c.id === id){
            return c;
        }
    }
    return null
}

const setImagesPerRow = (amount:number) => {
    cookies.set('images-per-row', amount);
}

const getImagesPerRow = ():number => {
    let amount = cookies.get('images-per-row');
    return amount != null ? Number(amount) : 4;
}


const PostService = {
    addCreator,
    removeAllCreators,
    getCreators,
    removeCreator,
    getImagesPerRow,
    setImagesPerRow,
    getPosts,
    getCreatorIds,
    getCreatorFromId,
    addCreators
}

export default PostService