import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';
import { Attachment, Post } from '../../types';
import InfiniteScroll from 'react-infinite-scroller';
import TagList from '../tag-list';
import addTags from '../add-tags';
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";
import Modal from 'react-modal'
import { Button } from '@mui/material';
import { Link } from 'react-router-dom';
import Image from './image';

const devMode = true;

const PostOverview: React.FC = () => {
    const [posts, setPosts] = useState<Post[]>([]);
    const [page, setPage] = useState<number>(0);
    const [isLoading, setLoadingState] = useState<boolean>(false);
    const [loadMessage, setLoadMessage] = useState<string>('Load more ...');
    const [imageViewerIsOpened, setImageViewerIsOpened] = useState<boolean>(false);
    const [videoIsOpen, setVideoIsOpen] = useState<boolean>(false);
    const [viewedImage, setViewedImage] = useState<Post|undefined>(undefined);
    const [imagesPerRow, setImagesPerRow] = useState<number>(3);
    const [showAllArtists, setShowAllArtists] = useState<boolean>(true);
    const [isPoolOpened, setPoolOpened] = useState<boolean>(false);
    const [poolAttachments, setPoolAttachments] = useState<Attachment[]>([])
    const [prevAndNextImage, setPrevAndNextImage] = useState<{prev:Post|undefined, next:Post|undefined}>({prev: undefined, next: undefined});
    const [isOpenedImageAPool, setIsImageAPool] = useState<boolean>(false);
    const [poolImageSize, setPoolImageSize] = useState<number>(100);
    const [screenWidth, setScreenWidth] = useState<number>(1000);
    const [nextPosts, setNextPosts] = useState<Post[]>([]);

    useEffect(() => {
        devMode && console.log('I fire once')
        setImagesPerRow(PostService.getImagesPerRow());
        window.addEventListener('resize', () => setScreenWidth(window.innerWidth))
    }, []);

    useEffect(() => {
        document.body.style.overflow = imageViewerIsOpened || isPoolOpened || videoIsOpen ? 'hidden' : 'unset';
    }, [imageViewerIsOpened, isPoolOpened, videoIsOpen])

    const loadMessageFormatter = (current:number, total:number, isComplex:boolean) => {
        let string = `Sending ${isComplex ? 'complex' : 'normal'} request ${current}/${total}`;
        setLoadMessage(string);
    }

    const showPool = async (event:any, attachments:Attachment[]) => {
        event.preventDefault();
        setPoolAttachments([])
        setPoolOpened(true);
        setPoolAttachments(attachments)
    }

    const getPosts = async () => {
        if(!isLoading){
            setLoadingState(true)
            let local_nextPosts = nextPosts;
            const post_amount = 20;
            if (local_nextPosts.length < post_amount){
                const incomingPosts = await getNextPosts();
                local_nextPosts = [...local_nextPosts, ...incomingPosts];
            }
            const postSection = local_nextPosts.slice(0, post_amount);
            local_nextPosts.splice(0, post_amount);
            setNextPosts(local_nextPosts);
            setPosts([...posts, ...postSection]);

            setTimeout(() => setLoadingState(false), 3000);
        }
    }

    const getNextPosts = async () => {
        devMode && console.log("Loading posts ...")

        const creators = PostService.getCreators()
        let uniquePosts = await PostService.getPosts(page, creators)

        devMode && console.log('sorting ...')
        uniquePosts.sort((a:Post, b:Post) => {
            let left = new Date(a.added);
            let right = new Date(b.added);
            if(left > right) {
                return -1;
            }
            if (right > left) {
                return 1;
            }
            return 0;
        })
        setLoadMessage('Load more ...')
        setPage(page + 1)
        return uniquePosts;
    };

    const getMore = (event:any) => {
        event.preventDefault();
        getPosts()
    }

    const refreshPosts = () => {
        devMode && console.log('Refreshing posts')
        window.location.reload()
    }

    const changeImagesPerRow = (amount:number) => {
        let newAmount = imagesPerRow + amount;
        if (newAmount > 0 && newAmount <= 10) {
            setImagesPerRow(newAmount);
            PostService.setImagesPerRow(newAmount);
        }
    }

    const imageViewer = (post:Post, isPool:boolean) => {
        setIsImageAPool(isPool);
        setViewedImage(post);
        if (false){
            setImageViewerIsOpened(false);
            setVideoIsOpen(true);
        } else {
            setVideoIsOpen(false);
            setImageViewerIsOpened(true);
        }
        const theseposts = posts;
        const prevIndex = theseposts.indexOf(post)-1;
        const prev = prevIndex < 0 ? undefined : theseposts[prevIndex];
        const nextIndex = prevIndex+2;
        const next = nextIndex > theseposts.length ? undefined : theseposts[nextIndex];
        setPrevAndNextImage({prev: prev, next: next});
    }

    const changePoolImageSize = (amount:number) => {
        let newAmount = poolImageSize + amount;
        if (newAmount > 0 && newAmount <= 100) {
            setPoolImageSize(newAmount);
        }
    }

    const modalStyle:Modal.Styles = {
        content: {
            backgroundColor: 'black',
            top: screenWidth <= 720 ? '0%' : '3%',
            bottom: screenWidth <= 720 ? '0%' : '3%',
            left: screenWidth <= 720 ? '0%' : '3%',
            right: screenWidth <= 720 ? '0%' : '3%'
        }
    };

    return (
        <>
            <div className='aside-grid-container'>
                <div>
                    <Link to="/kemono/profile">Profile</Link>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={() => changeImagesPerRow(-1)}>{'<<'}</a></p>
                        <p>{imagesPerRow}</p>
                        <p><a href='#' onClick={() => changeImagesPerRow(1)}>{'>>'}</a></p>
                    </div>
                    <div>
                        <input type='checkbox' id='show-artists' onClick={() => setShowAllArtists(!showAllArtists)} checked={showAllArtists} />
                        <label htmlFor='show-artists'>Always show info</label>
                    </div>
                    <addTags.AddTags refreshPosts={refreshPosts}/>
                    <TagList/>
                </div>
                <div style={{textAlign: 'center'}}>
                    <div style={{display: 'flex', flexWrap: 'wrap'}}>
                        {posts.map((post, index) => (
                            <Image post={post} onClick={() => imageViewer(post, false)} key={index} showAllArtists={showAllArtists} imagesPerRow={imagesPerRow} showPool={showPool}/>
                        ))}
                    </div>
                    <InfiniteScroll
                        hasMore={true}
                        loadMore={getPosts}
                    />
                    <h1><a href='#' onClick={getMore}>{loadMessage}</a></h1>
                </div>
            </div>
            {imageViewerIsOpened && <Lightbox
                mainSrc={viewedImage === undefined ? '' : `https://kemono.party${viewedImage?.file.path}`}
                mainSrcThumbnail={`https://kemono.party/thumbnail/data${viewedImage?.file.path}`}
                onCloseRequest={() => setImageViewerIsOpened(false)}
                nextSrc={`https://kemono.party${prevAndNextImage.next?.file.path}`}
                nextSrcThumbnail={`https://kemono.party/thumbnail/data${prevAndNextImage.next?.file.path}`}
                prevSrc={`https://kemono.party${prevAndNextImage.prev?.file.path}`}
                prevSrcThumbnail={`https://kemono.party/thumbnail/data${prevAndNextImage.prev?.file.path}`}
                onMoveNextRequest={() => {if (prevAndNextImage.next !== undefined) imageViewer(prevAndNextImage.next, isOpenedImageAPool)}}
                onMovePrevRequest={() => {if (prevAndNextImage.prev !== undefined) imageViewer(prevAndNextImage.prev, isOpenedImageAPool)}}
            />}
            <Modal isOpen={isPoolOpened} onRequestClose={() => setPoolOpened(false)} ariaHideApp={false} style={modalStyle}>
                <div style={{display: 'flex', justifyContent: 'space-between'}}>
                    <div className='modal-button'>
                        <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                    </div>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(-5)}}>{'<<'}</a></p>
                        <p style={{padding: '0 1rem'}}>{poolImageSize}</p>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(5)}}>{'>>'}</a></p>
                    </div>
                </div>
                <div>
                    {poolAttachments.map((attachment, index) => (
                        <img width={`${poolImageSize}%`} key={index} src={`https://kemono.party/thumbnail/data${attachment.path}`} alt={String(attachment.name)} onClick={() => null /*imageViewer(post, true)*/} />
                    ))}
                </div>
                <div className='modal-button' style={{margin: '1rem 0 0 0', display: 'flex', justifyContent: 'center'}}>
                    <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                </div>
            </Modal>
        </>
    )
}

export default PostOverview